export default function Testimonials() {
    const reviews = [
        {
            name: "Aarav P.",
            text: "I've tried many herbal teas, but Vedic Brew is on another level. The freshness is undeniable.",
            rating: 5,
        },
        {
            name: "Sarah J.",
            text: "The 'Natural Energy' blend actually works! No jitters, just focus.",
            rating: 5,
        },
        {
            name: "Michael K.",
            text: "Love the packaging and the taste. Truly premium.",
            rating: 4,
        },
    ];

    return (
        <section id="testimonials" className="py-24 bg-base-100">
            <div className="container-wrapper">
                <div className="text-center mb-16">
                    <h2 className="text-4xl font-bold font-display text-base-content">What They Say</h2>
                </div>
                <div className="grid md:grid-cols-3 gap-8">
                    {reviews.map((r, i) => (
                        <div key={i} className="card bg-base-200 p-8 shadow-sm">
                            <div className="rating rating-sm mb-4">
                                {[...Array(5)].map((_, star) => (
                                    <input key={star} type="radio" name={`rating-${i}`} className="mask mask-star-2 bg-brand-accent" checked={star < r.rating} readOnly />
                                ))}
                            </div>
                            <p className="italic text-gray-600 mb-6">"{r.text}"</p>
                            <div className="flex items-center gap-4">
                                <div className="avatar placeholder">
                                    <div className="bg-neutral text-neutral-content rounded-full w-10">
                                        <span className="text-xs">{r.name.charAt(0)}</span>
                                    </div>
                                </div>
                                <span className="font-bold">{r.name}</span>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}
