export default function Ingredients() {
    return (
        <section id="ingredients" className="py-24 bg-brand-primary/5">
            <div className="container-wrapper">
                <div className="text-center mb-16">
                    <h2 className="text-4xl font-bold font-display text-base-content">Pure Ingredients</h2>
                    <p className="text-gray-500 mt-4">No fillers. Just nature.</p>
                </div>
                <div className="flex flex-wrap justify-center gap-6">
                    {/* Visual simulation of ingredients */}
                    {["Turmeric", "Ginger", "Black Pepper", "Holy Basil", "Cinnamon"].map((item, i) => (
                        <div key={i} className="badge badge-lg badge-outline p-4 text-lg border-brand-primary text-brand-primary gap-2">
                            {item}
                        </div>
                    ))}
                </div>
                <div className="mt-12 text-center">
                    <p className="italic text-gray-500">Full nutritional label available on package.</p>
                </div>
            </div>
        </section>
    );
}
