export default function FAQ() {
    const faqs = [
        { q: "Is this 100% organic?", a: "Yes, our tea is certified organic by USDA and India Organic." },
        { q: "How long does shipping take?", a: "We ship within 24 hours. Delivery usually takes 3-5 business days." },
        { q: "What is your return policy?", a: "We offer a 30-day money-back guarantee if you are not satisfied." },
    ];

    return (
        <section id="faq" className="py-24 bg-base-200">
            <div className="container-wrapper max-w-3xl">
                <div className="text-center mb-16">
                    <h2 className="text-4xl font-bold font-display text-base-content">Frequently Asked Questions</h2>
                </div>
                <div className="flex flex-col gap-4">
                    {faqs.map((f, i) => (
                        <div key={i} className="collapse collapse-plus bg-base-100 shadow-sm border border-base-300">
                            <input type="radio" name="my-accordion-3" defaultChecked={i === 0} />
                            <div className="collapse-title text-xl font-medium">
                                {f.q}
                            </div>
                            <div className="collapse-content">
                                <p className="text-gray-600">{f.a}</p>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}
